
.concord-image-wrapper {

  height : 700px;
}

.concord-img {
  width: 100%;
  height: 700px;
  object-fit: cover;
}

.concord-img-gradient {
    background: rgba(0,0,0,.4);
    background-position-x: initial;
    background-position-y: initial;
    background-size: initial;
    background-repeat: initial;
    background-attachment: initial;
    background-origin: initial;
    background-clip: initial;
    background-color: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 60%,rgba(0,0,0,.8) 100%);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height : inherit;
}

#logo {
    position: absolute;
    top: 10px;
    bottom: 0;
    right: 0;
    left: 50px;
    height: 75px;
    width: 135px;
    opacity : 0.85;
}

#drop {
    position: absolute;
    top: 26px;
    bottom: auto;
    right: 200px;
    left: auto;
}

#dropdownMenuButton {
    height : 35px;
    color : white;
    font-size: 14px;
    background: transparent;
    border-radius: 2px;
    border-color: rgb(238, 241, 243);
}

#globe {
    margin-right: 5px;
}

#signIn {
    color : white;
    background-color: rgb(233, 45, 12);
    border-radius: 2px;
    position: absolute;
    top: 26px;
    bottom: auto;
    right: 75px;
    left: auto;
    height : 35px;
    width : 90px;
}
.container-a {
    position: absolute;
    top: 14%;
    left: 15%;
    width: 100%;
    padding: 85px 0;
    padding-bottom: 0;
    max-width: 950px;
    margin: 0 auto;
    text-align: center;
    color: white;
}


.heading {
    justify-content: center;
    text-align: center;
    align-items: center;
    margin : auto 100px;
}

h1 {
    font-size: 48px;
    font-weight: bolder;
    padding : 70px;
    padding-bottom: 10px;
    letter-spacing: 0.2px;
}

h3 {
    font-size : 26px;
    padding-top: 6px;
    padding-bottom: 28px;
}

h5{
    padding-bottom: 18px;
}

#fname {
    width : 50%;
    height : 60px;
    padding : 10px;
    display : inline-block;
}

#getStarted {
    color : white;
    background-color: rgb(245, 3, 3);
    height : 58px;
    width : 22%;
    border-radius: 0;
}

#arrow {
    margin-left : 10px;
    width : 22px;
    height: 22px;
}