.faq-container {
    height: 920.16px;
    margin: 0;
    padding : 70px 45px;
}
.faq-insider {
    height: 100%;
    color: white;
}
.faq-text-container {
    margin-top: 0;
    margin-bottom: 2%;
}
.faq-heading {
    margin-top: 0;
    padding-top: 0;
}
.accordion {
    position: absolute;
    left : 18%;
    width: 62%;
}

.accordion-button {
    height: 71px;
    padding : 0 4%;
    color: white;
    background-color: rgb(63, 62, 62);
    font-size: 24px;
    font-weight:200;
}
.accordion-item {
    height: auto;
    padding : 0 0;
    margin: 10px;
    background-color: rgb(63, 62, 62);
    font-size: 24px;
    font-weight:200;
}
#a {
    margin: 0 auto;
    position:relative;
    top : -25%;
    left : auto;
    margin: 0;
}
#last-btn {
    width: 1000px;
    height: 250px;
    margin: 0 -10%;
    background-color: black;
}